import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/views/Home.vue'
import RoomInfo from '@/views/RoomInfo.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/room/:roomId',
      name: 'RoomInfo',
      // component: () => import('@/views/RoomInfo.vue')
      component: RoomInfo
    }
  ]
})



